.recipe{
    border-radius: 10px;
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
    align-items: center;
    min-width: 40%;
}

.image{
    border-radius: 5%;
}